@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.eazy-win {
    border: 1px solid #e8e8e8;
    border-radius: 4px;
    background-color: rgb(249, 249, 249);
    box-shadow: rgb(92 128 255 / 12%) -200px 0px 200px, rgb(168 255 128 / 12%) 140px 50px 100px;
    max-height: 100%;
    overflow: hidden;
    &-header {
        height: 28px;
        display: flex;
        align-items: center;
        padding-left: 12px;
        padding-right: 12px;
    }

    &-body {
        padding: 8px 12px 12px;
    }
}

.win-controll-container {
    display: flex;
}

.win-controll {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 6px;
    &-close {
        background-color: rgb(238, 108, 94);
    }
    &-min {
        background-color: rgb(246, 191, 80);
    }
    &-full {
        background-color: rgb(98, 198, 85);
    }
}

.eazy-win-dark {
    background-color: rgb(56, 56, 56);
}